.FormSubmit {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: var(--accent-color);
  border: solid 1px rgba(0, 0, 0, 0.05);
  border-radius: 3px;
  -webkit-box-shadow: inset 0px -4px 0 0 rgba(0, 0, 0, .2);
          box-shadow: inset 0px -4px 0 0 rgba(0, 0, 0, .2);
  color: var(--bright-text-color);
  display: block;
  font-family: var(--roboto);
  font-size: 1rem;
  font-weight: 500;
  letter-spacing: 1px;
  margin: 0 auto;
  min-width: 8rem;
  padding: 1rem 2rem;
  text-transform: uppercase;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}

.FormSubmit[disabled] {
  background: #aaa;
  color: #ddd;
  cursor: not-allowed;
  pointer-event: none;
}

.FormSubmit:not([disabled]):hover {
  background: var(--accent-color-darker);
  -webkit-box-shadow: inset 0 -2px 0 0 rgba(0, 0, 0, .2);
          box-shadow: inset 0 -2px 0 0 rgba(0, 0, 0, .2);
  cursor: pointer;
  -webkit-transform: translateY(2px);
      -ms-transform: translateY(2px);
          transform: translateY(2px);
}

.FormSubmit:not([disabled]):focus {
  background: var(--accent-color-darker);
  outline: none;
}

.FormSubmit:not([disabled]):active {
  -webkit-box-shadow: inset 0 0 0 0 rgba(0, 0, 0, .2);
          box-shadow: inset 0 0 0 0 rgba(0, 0, 0, .2);
  -webkit-transform: translateY(4px);
      -ms-transform: translateY(4px);
          transform: translateY(4px);
}



